import type * as Party from "partyserver";

export interface Env {
  AI: any; // Cloudflare Workers AI binding
  SUPABASE_URL: string;
  SUPABASE_KEY: string;
}

interface ChatMessage {
  id: string;
  document_id: string;
  user_id: string;
  role: "user" | "assistant" | "system";
  content: string;
  metadata?: any;
  created_at: string;
}

export default class ChatRoom implements Party.Server {
  constructor(readonly party: Party.Party, readonly env: Env) {}

  async onConnect(conn: Party.Connection, ctx: Party.ConnectionContext) {
    // Extract document_id from room ID
    const documentId = this.party.id;

    console.log(
      `[ChatRoom] User ${conn.id} connected to document ${documentId}`
    );

    // Send chat history to newly connected client
    await this.sendChatHistory(conn, documentId);
  }

  async onMessage(message: string, sender: Party.Connection) {
    try {
      const data = JSON.parse(message);
      const documentId = this.party.id;

      switch (data.type) {
        case "chat_message":
          await this.handleChatMessage(data, sender, documentId);
          break;

        case "request_history":
          await this.sendChatHistory(sender, documentId);
          break;

        default:
          console.warn(`[ChatRoom] Unknown message type: ${data.type}`);
      }
    } catch (error) {
      console.error("[ChatRoom] Error processing message:", error);
      sender.send(
        JSON.stringify({
          type: "error",
          error: "Failed to process message",
        })
      );
    }
  }

  async onClose(conn: Party.Connection) {
    console.log(`[ChatRoom] User ${conn.id} disconnected`);
  }

  /**
   * Handle incoming chat message from user
   */
  private async handleChatMessage(
    data: any,
    sender: Party.Connection,
    documentId: string
  ) {
    const { content, user_id, metadata } = data;

    if (!content || !user_id) {
      sender.send(
        JSON.stringify({
          type: "error",
          error: "Missing required fields: content, user_id",
        })
      );
      return;
    }

    // 1. Save user message to Supabase
    const userMessage = await this.saveMessage({
      document_id: documentId,
      user_id,
      role: "user",
      content,
      metadata,
    });

    // 2. Broadcast user message to all connected clients
    this.party.broadcast(
      JSON.stringify({
        type: "message",
        message: userMessage,
      })
    );

    // 3. Generate AI response with streaming
    await this.generateAIResponse(documentId, user_id, content);
  }

  /**
   * Generate AI response using Workers AI with streaming
   */
  private async generateAIResponse(
    documentId: string,
    userId: string,
    userMessage: string
  ) {
    try {
      // Get recent chat history for context
      const history = await this.getChatHistory(documentId, 10);

      // Build conversation context
      const messages = [
        {
          role: "system",
          content: "You are a helpful travel planning assistant. Help users plan their trips, suggest destinations, and provide travel advice.",
        },
        ...history.map((msg) => ({
          role: msg.role === "assistant" ? "assistant" : "user",
          content: msg.content,
        })),
      ];

      // Stream AI response
      const response = await this.env.AI.run("@cf/meta/llama-3.1-8b-instruct", {
        messages,
        stream: true,
      });

      let fullResponse = "";
      const messageId = crypto.randomUUID();

      // Send streaming chunks to all clients
      for await (const chunk of response) {
        if (chunk.response) {
          fullResponse += chunk.response;

          // Broadcast chunk to all connected clients
          this.party.broadcast(
            JSON.stringify({
              type: "ai_chunk",
              message_id: messageId,
              chunk: chunk.response,
              done: false,
            })
          );
        }
      }

      // Save complete AI response to Supabase
      const aiMessage = await this.saveMessage({
        document_id: documentId,
        user_id: userId, // Associate with user who asked
        role: "assistant",
        content: fullResponse,
        metadata: { model: "llama-3.1-8b-instruct" },
      });

      // Send completion message
      this.party.broadcast(
        JSON.stringify({
          type: "ai_chunk",
          message_id: messageId,
          chunk: "",
          done: true,
          message: aiMessage,
        })
      );
    } catch (error) {
      console.error("[ChatRoom] Error generating AI response:", error);
      this.party.broadcast(
        JSON.stringify({
          type: "error",
          error: "Failed to generate AI response",
        })
      );
    }
  }

  /**
   * Send chat history to a specific connection
   */
  private async sendChatHistory(conn: Party.Connection, documentId: string) {
    try {
      const history = await this.getChatHistory(documentId, 50);

      conn.send(
        JSON.stringify({
          type: "history",
          messages: history,
        })
      );
    } catch (error) {
      console.error("[ChatRoom] Error sending history:", error);
      conn.send(
        JSON.stringify({
          type: "error",
          error: "Failed to load chat history",
        })
      );
    }
  }

  /**
   * Get chat history from Supabase
   */
  private async getChatHistory(
    documentId: string,
    limit: number = 50
  ): Promise<ChatMessage[]> {
    const response = await fetch(
      `${this.env.SUPABASE_URL}/rest/v1/document_chats?document_id=eq.${documentId}&order=created_at.asc&limit=${limit}`,
      {
        headers: {
          apikey: this.env.SUPABASE_KEY,
          Authorization: `Bearer ${this.env.SUPABASE_KEY}`,
        },
      }
    );

    if (!response.ok) {
      throw new Error(`Failed to fetch chat history: ${response.statusText}`);
    }

    return response.json();
  }

  /**
   * Save message to Supabase
   */
  private async saveMessage(message: Omit<ChatMessage, "id" | "created_at">): Promise<ChatMessage> {
    const response = await fetch(
      `${this.env.SUPABASE_URL}/rest/v1/document_chats`,
      {
        method: "POST",
        headers: {
          apikey: this.env.SUPABASE_KEY,
          Authorization: `Bearer ${this.env.SUPABASE_KEY}`,
          "Content-Type": "application/json",
          Prefer: "return=representation",
        },
        body: JSON.stringify({
          ...message,
          created_at: new Date().toISOString(),
        }),
      }
    );

    if (!response.ok) {
      throw new Error(`Failed to save message: ${response.statusText}`);
    }

    const [savedMessage] = await response.json();
    return savedMessage;
  }
}
